/*------------------------後台設定開始----------------------------*/
.bg-backstage{
    background: url("../images/backstage-bg.jpg");
    background-size: cover;
    min-height: calc(100vh - 48px);
}
.navbar[data-con="backstage"] {
	transition: all 0.2s ease;
	padding-top: 0;
	padding-bottom: 0;
	&-brand {
		width: 159px;
		@include mobile-lg {
			width: 130px;
		}
	}
	&-nav {
		display: flex;
		align-items: center;
		@include PC {
			width: 100%;
		}
	}
	.navbar-toggler {
        align-items: center;
		color: $white;
		border-radius: 0;
		min-width: 48px;
		font-size: $font-xxxs;
        height: 48px;
        img{
            width: 28px;
        }
        @include PC-sm{
            display: flex;
        }
		@include mobile-md {
			min-width: auto;
		}
		&:hover {
			background: $secondary-dark-color
		}
		&:focus {
			box-shadow: none;
		}
	}
	.navbar-collapse {
        @include PC-sm {
            padding: 0 !important;
            position: absolute;
        width: 100%;
        left: 0;
        top: 48px;
        }
	}
	.btn-input-group {
		@include PC {
			width: 200px;
		}
		@include pad {
			width: 100%;
			max-width: 300px;
			margin: 0 auto;
		}
	}
	.nav-item {
        position: relative;
        &::after{
            content:"";
            position: absolute;
            display: inline-block;
            width: 1px;
            height: 28px;
            background: $white;
            right: 0;
            top: calc(50% - 14px);
        }
        &:last-child{
            &:after{
                display: none;
            }
        }
		@include PC {
			width: 100%;
			display: flex;
			justify-content: center;
			border-bottom: 1px solid $secondary-color;
			flex-wrap: wrap;
		}
	}
	.nav-link {
		padding: 12px 24px;
		color: $white;
		font-size: $font-xs;
		display: flex;
		align-items: center;
		border-radius: 0 !important;
        flex-direction: row;
        position: relative;
        background: none !important;
        @include PC-sm{
            color: $secondary-dark-color !important;
            background: $white !important;
            img{
                filter: invert(47%) sepia(97%) saturate(342%) hue-rotate(183deg) brightness(103%) contrast(93%);
            }
        }
		&:after{
            content:"";
            width: calc(100% - 16px);
            height: 3px;
            position: absolute;
            left: 8px;
            bottom: 6px;
            background: $white;
            display: none;
            transition: all 0.2s ease;
        }
		&:hover {
            opacity: 1;
			&::after{
                display: block;
            }
		}
        img {
			width: 24px;
            margin-right: 4px;
		}
        p{
            white-space: nowrap;
        }
	}
    .nav-close-btn{
        width: 48px;
        height: 48px;
        padding: 0;
        border-radius: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            width: 20px;
        }
    }
}
.backstage{
    margin-top: 48px;
    padding-top: 56px;
    padding-bottom: 56px;
    &-header{
        display: flex;
        flex-wrap: wrap;
    }
    &-title{
        color: $secondary-xdark-color;
        display: flex;
        font-size: $font-lg;
        align-items: center;
        @include pad{
            font-size: $font-md;
            img{
                width: 20px;
            }
        }
    }
    .input-group{
        &>*{
            height: 32px;
        }
        input{
            border-radius: 10px 0 0 10px;
            padding-top: 4px;
            &::placeholder{
                font-size: $font-xs;
            }
        }
        button{
            border-radius: 0 10px 10px 0 !important;
        }
    }
    #filter{
        position: absolute;
        width: 190px;
        left: 0;
        top: 32px;
        z-index: 100;
        label,select{
            font-size: $font-xs;
        }
    }
    .paging-txt{
        font-size: $font-xs;
        margin-top: -30px;
        position: relative;
        z-index: 100;
    }
}
.table-backstage{
    background: none;
    th{
        background: $secondary-color;
        color: $white;
    }
    tbody{
        background: $white;
    }
    .footable-pagination-wrapper{
        display: flex;
    }
    .pagination{
        margin-left: auto;
    }
}