/*------------------------登入頁設定開始----------------------------*/
.wrapper[data-bg="login"]{
  min-height: 100vh;
  background: url("../images/login-bg.jpg") no-repeat;
  background-size: cover;
}

.login{
  display: flex;
  min-height: 100vh;
  align-items: center;
  padding-top: 96px;
  @include PC{
    padding-top: 0;
  }
  @include PC-sm{
    padding-top: 96px;
    align-items: flex-start;
    min-height: auto;
  }
  @include mobile-md{
    padding-top: 80px;
  }
  &-brand{
    width: 240px;
    position: absolute;
    right: 56px;
    top: 56px;
    @include PC-lg{
      width: 220px;
      right: 24px;
      top: 24px;
    }
    @include pad{
      width: 200px;
    }
    @include mobile-lg{
      width: 150px;
    }
  }
  &-bottom-logo{
    width: 450px;
    position: absolute;
    left: 56px;
    bottom: 56px;
    @include PC-lg{
      width: 400px;
      left: 24px;
      bottom: 24px;
    }
    @include PC-sm{
      position: initial;
      left: auto;
      bottom: auto;
      margin-bottom: 24px;
    }
    @include pad{
      width: 350px;
    }
    @include mobile-lg{
      width: 300px;
    }
  }
  &-title{
    font-size: $font-xxl;
    font-weight: $font-heavy;
    color: $white;
    @include PC-lg{
      font-size: $font-xl;
    }
    @include mobile-lg{
      font-size: $font-lg;
    }
    span{
      font-size: $font-xl;
      font-weight: $font-light;
       @include PC-lg{
      font-size: $font-lg;
    }
    @include mobile-lg{
      font-size: $font-md;
    }
    }
  }
  &-vision{
    width: 90%;
    max-width: 920px;
    @include PC{
      max-width: 600px;
    }
  }
  &-function{
    display: block;
    width: 25px;
    @include PC-sm{
      width: 20px;
    }
    @include mobile-lg{
      width: 68px;
      display: flex;
      justify-content: space-between;
    }
    &-item{
      width: 100%;
      @include mobile-lg{
      width: 30px;
    }
    }
    &-link{
      width: 100%;
      height: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
       @include PC-sm{
        height: 20px;
      }
      @include mobile-lg{
        width: 30px;
        height: 45px;
      }
      img{
        width: 100%;
        @include mobile-lg{
          width: 30px;
        }
      }
    }
  }
  &-verify{
    display: flex;
    @include PC{
      flex-wrap: wrap;
    }
    input{
      margin-right: 8px;
      @include PC{
        width: calc(100% - 90px);
        margin-bottom: 10px;
        margin-right: 0;
      }
      @include PC-sm{
        width: auto;
        min-width: 100px;
        margin-right: 8px;
        flex: 1 1;
      }
    }
    label{
      margin-bottom: 0;
      @include PC{
        margin-bottom: 10px;
      }
    }
    &-img{
      @include PC{
        width: 100%;
        justify-content: flex-end;
      }
      @include PC-sm{
        width: auto;
      }
      @include mobile-lg{
        width: 100%;
      }
      &>img{
        width: 150px;
        height: 55px;
        @include PC-sm{
        height: 45px;
      }
      }
    }
  }
  &-btn{
    border-radius: 15px !important;
    height: 65px;
    font-size: $font-lg;
    @include PC-lg{
      height: auto;
    }
    @include mobile-lg{
      font-size: $font-md;
    }
  }
  input:not([type="checkbox"]){
    border-radius: 15px;
    border: none;
    background: $white;
    padding-left: 20px;
    height: 55px;
    font-size: $font-smd;
    @include PC-md{
      height: auto;
    }
    &::placeholder{
      color: $primary-color!important;
      font-size: $font-smd;
      @include mobile-md{
        font-size: $font-sm;
      }
    }
    &[data-con="account"]{
      background: url("../images/ic-account.svg") no-repeat right center $white;
      padding-right: 55px;
      @include mobile-lg{
        background-size: 40px;
      }
    }
    &[data-con="password"]{
      background: url("../images/ic-password.svg") no-repeat right center $white;
      padding-right: 55px;
      @include mobile-lg{
        background-size: 40px;
      }
    }
  }
  form>div{
    margin-bottom: 40px;
    @include PC-lg{
      margin-bottom: 24px;
    }
  }
}
