/* --------------顏色設定開始--------------- */

$primary-color:#29ABE2;
$primary-dark-color:#0071BC;
$secondary-color:#87ADF9;
$secondary-dark-color:#6595F6;
$secondary-xdark-color:#5C719C;
$tertiary-color:#6BC2FF;
$tertiary-light-color:#B3DEFF;
$tertiary-xlight-color:#E5F2FF;
$tertiary-xxlight-color:#F2F8FF;
$quaternary-color:#649C19;
$quaternary-light-color:#8CC63F;
$quaternary-xlight-color:#EEF7E2;

$primary-weak-color:#72B7F3;
$primary-light-color:#9FD3F5;
$primary-xlight-color:#D9F0FF;
$primary-xxlight-color:#F2FBFB;
$secondary-pale-color:#F5FBF4;
$base-color:#72889C;
$base-light-color:#9FB1C2;
$special-color:#727AE9;
$pale-color:#F5F6FA;
$danger-color:#E85500;
$assist-color:#ABA470;
$assist-dark-color:#948F67;
$pale-light-color:#f8f8f7;

$black:#000;
$gray900:#292929;
$gray800:#525252;
$gray700:#7a7a7a;
$gray600:#a3a3a3;
$gray500:#ccc;
$gray400:#d6d6d6;
$gray300:#e0e0e0;
$gray200:#ebebeb;
$gray100:#f5f5f5;
$white:#fff;


$color-themes: (
  //屬性: key, value
  'primary-color': $primary-color,
  'primary-dark-color': $primary-dark-color,
  'primary-light-color': $primary-light-color,
  'primary-xlight-color': $primary-xlight-color,
  'primary-xxlight-color': $primary-xxlight-color,
  'primary-weak-color': $primary-weak-color,
  'base-color': $base-color,
  'base-light-color': $base-light-color,
  'secondary-color': $secondary-color,
  'secondary-dark-color': $secondary-dark-color,
  'secondary-xdark-color': $secondary-xdark-color,
  'secondary-pale-color': $secondary-pale-color,
  'tertiary-color': $tertiary-color,
  'tertiary-xlight-color': $tertiary-xlight-color,
  'tertiary-xxlight-color': $tertiary-xxlight-color,
  'quaternary-color': $quaternary-color,
  'quaternary-light-color': $quaternary-light-color,
  'quaternary-xlight-color': $quaternary-xlight-color,
  'assist-color': $assist-color,
  'assist-dark-color': $assist-dark-color,
  'danger-color': $danger-color,
  'pale-color': $pale-color,
  'pale-light-color': $pale-light-color,
  'special-color': $special-color,
  'black': $black,
  'gray900': $gray900,
  'gray800': $gray800,
  'gray700': $gray700,
  'gray600': $gray600,
  'gray500': $gray500,
  'gray400': $gray400,
  'gray300': $gray300,
  'gray200': $gray200,
  'gray100': $gray100,
  'white': $white,
);
/* --------------字型設定開始--------------- */

$font-xxxs:.7rem;
$font-xxs:.8rem;
$font-xs:.9rem;
$font-xsm:.95rem;
$font-sm:1rem;
$font-smd:1.125rem;
$font-md:1.2rem;
$font-lg:1.4rem;
$font-xl:1.8rem;
$font-xxl:2.5rem;
$font-xxxl:3rem;


$font-size-themes: (
  //屬性: key, value
  'font-xxxs': $font-xxxs,
  'font-xxs': $font-xxs,
  'font-xs': $font-xs,
  'font-xsm': $font-xsm,
  'font-sm': $font-sm,
  'font-smd': $font-smd,
  'font-md': $font-md,
  'font-lg': $font-lg,
  'font-xl': $font-xl,
  'font-xxl': $font-xxl,
  'font-xxxl': $font-xxxl,
);


$font-xlight:100;
$font-light:400;
$font-bold:500;
$font-heavy:600;
$font-xheavy:800;

$font-weight-themes: (
  //屬性: key, value
  'font-xlight': $font-xlight,
  'font-light': $font-light,
  'font-bold': $font-bold,
  'font-heavy': $font-heavy,
  'font-xheavy': $font-xheavy,
);


@mixin gradient-horizon($primary-color,$gray) {
background: $gray; /* Old browsers */
background: -moz-linear-gradient(left,  $primary-color 0%, $gray 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(left,  $primary-color 0%,$gray 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to right,  $primary-color 0%,$gray 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

}

/* 字體設定 */
$font1: 微軟正黑體, Noto Sans TC, sans-serif;
$font2: Noto Sans TC, 微軟正黑體, sans-serif;
$font-en: arial, sans-serif;

/* --------------按鈕設定開始--------------- */

@mixin btn-dark-mix($bgcolor, $txt) {
  color: $txt;
  background: $bgcolor;
  &:hover {
      color: $txt;
      background: darken($color: $bgcolor, $amount: 5%);
  }
}
@mixin btn-light-mix($bgcolor, $txt) {
  color: $txt;
  background: $bgcolor;
  &:hover {
      color: $txt;
      background: lighten($color: $bgcolor, $amount: 5%);
  }
}
@mixin btn-border($color){
  color: $color;
  border: 1px solid $color;
  padding: 5px 15px;
  &:hover,&.active{
    color: $white;
    background: $color;
    border: 1px solid $color;
  }
}
@mixin btn-border-mix($color,$bdcolor){
  color: $color;
  border: 1px solid $bdcolor;
  padding: 5px 15px;
  background: $white;
  &:hover,&.active{
    color: $white;
    background: $bdcolor;
    border: 1px solid $bdcolor;
  }
}
@mixin btn-blank($color){
  color: $color;
  background: $white;
  padding: 5px 15px;
  text-align: center;
  &:hover{
    color: $white;
    background: $color;
  }
}
/*連結設定開始*/
@mixin txt-link-dark($color){
  color: $color;
  &:hover{
    color: darken($color: $color, $amount: 10%);
  }
}
@mixin txt-link-light($color){
  color: $color;
  &:hover{
    color: lighten($color: $color, $amount: 15%);
  }
}
/*標題設定開始*/
@mixin top-title($color) {
  font-size: $font-xl;
  letter-spacing: 8px;
  font-weight: 600;
  color:$color;
  position: relative;
  text-align: center;
  padding-bottom: 12px;
  margin-bottom: 30px;
  @include pad{
    font-size:$font-lg;
    margin-bottom: 22px;
  }
  &:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background: $color;
    left: 0;
    bottom: 0;
  }
}
/*表格設定開始*/
@mixin table-simple($bgcolor,$bdcolor){
  font-size: $font-sm;
  border: 2px solid $bdcolor;
  th{
      background: $bgcolor;
      border-bottom: 2px solid $bdcolor!important;
      color: $white;
      font-weight: $font-light;
      @include pad{
        padding: .5rem;
      }
  }
  td{
    border-top: 1px solid $bdcolor;
    vertical-align: middle;
    @include pad{
      padding: .5rem;
    }
  }
  td,th{
    border-color: $bdcolor;
  }
}