/*-----------------連結樣式---------------------*/
@each $key, $value in $color-themes {
    .link-#{$key} {
        color: $value;
        &:hover {
            color: darken($value, 20%);
            img{
                filter: brightness(0.8);
            }
        }
    }
}
.link-img{
    &:hover{
        opacity: 0.8;
    }
}
.link-shadow {
    &:hover {
        box-shadow: 0 0 10px $gray300 !important;
    }
}
/*-----------------文字樣式---------------------*/
@each $key, $value in $color-themes {
    //文字顏色
    .#{$key} {
        color: $value !important;
    }
}
@each $key, $value in $font-size-themes {
    //文字尺寸
    .#{$key} {
        font-size: $value !important;
    }
}
@each $key, $value in $font-size-themes {
    //文字尺寸
    .#{$key}-lg {
        @include PC {
            font-size: $value !important;
        }
    }
    .#{$key}-md {
        @include pad {
            font-size: $value !important;
        }
    }
}
@each $key, $value in $font-weight-themes {
    //文字重量
    .#{$key} {
        font-weight: $value !important;
    }
}
.txt-shadow {
    text-shadow: 0 0 5px $gray500;
}
.ls-0{
    letter-spacing: normal;
}
/*-----------------背景樣式---------------------*/
@each $key, $value in $color-themes {
    .bg-#{$key} {
        background: $value !important;
    }
}
/*-----------------按鈕樣式---------------------*/
@each $key, $value in $color-themes {
    .btn-dark-#{$key} {
        @include btn-dark($value);
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
@each $key, $value in $color-themes {
    .btn-border-#{$key} {
        @include btn-border($value);
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
.toggle-menu {
    display: flex;
    border-bottom: 5px solid $secondary-color;
    button{
        border-radius: 5px 5px 0 0;
        background: darken($gray300,10%);
        color: $white;
        letter-spacing: 1px;
        &:hover{
            color: $white;
        }
        &.active {
            background: $secondary-color !important;
            border: 1px solid $secondary-color !important;
            pointer-events: none;
        }
    }
}
.circle-btn{
    width: 32px;
    height: 32px;
    border-radius: 50% !important;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.icon-btn{
    display: inline-block;
    width: 24px;
    vertical-align: inherit;
    margin: 0 5px;
    cursor: pointer;
    &:hover{
        opacity: 0.8;
    }
}
.dashed-btn{
    border: 2px dashed $quaternary-light-color;
    border-radius: 10px;
    color: $quaternary-light-color;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $white;
    &:hover{
        border: 2px dashed $quaternary-color;
        color: $quaternary-color;
        img{
            filter: invert(53%) sepia(88%) saturate(397%) hue-rotate(44deg) brightness(84%) contrast(93%);
        }
    }
}
.icon-txt-btn{
    display: flex;
    align-items: center;
    justify-content: center;
}
.outline-btn-group{
    display: flex;
    border: 1px solid $secondary-color;
    border-radius: 10px;
    height: 32px;
    &-item{
        display: flex;
        position: relative;
        &-link{
            color: $secondary-dark-color;
            font-size: $font-xs;
            display: flex;
            align-items: center;
            padding: 0 8px;
            height: 100%;
            img{
                width: 20px;
            }
        }
        &:after{
            content: "";
            display: block;
            position: absolute;
            right: 0;
            top: calc(50% - 10px);
            width: 1px;
            height: 20px;
            background: $secondary-color;
        }
        &:last-child{
            &:after{
                display: none;
            }
        }
    }
    
}
/*-----------------邊框樣式---------------------*/
@each $key, $value in $color-themes {
    .bd-#{$key} {
        border-color: $value !important;
    }
}
/*-----------------標題樣式---------------------*/
.main-title {
    background: $tertiary-color;
    color: $white;
    font-size: $font-lg;
    font-weight: $font-bold;
    position: relative;
    text-align: center;
    border-left: 10px solid $tertiary-color;
}
.subtitle {
    color: $tertiary-color;
    display: block;
    padding: 8px 24px 8px 0;
    position: relative;
    font-size: $font-md;
    font-weight: $font-heavy;
    &-txt{
        background: $white;
        display: inline-block;
        position: relative;
        z-index: 5;
        padding-right: 10px;
    }
    &:before{
        content:"";
        width: 100%;
        height: 1px;
        background: $tertiary-color;
        position: absolute;
        left: 0;
        top: 50%;
    }
}
.modal-body-title{
    @include pad{
        font-size: $font-sm;
        img{
            width: 20px;
        }
    }
}
/*-----------------列表樣式---------------------*/
.number-list {
    list-style-type: decimal;
    padding-left: 25px;
    color: lighten($gray700,10%);
    li{
        margin-bottom: 30px;
    }
    li::marker{
        color: $assist-color;
    }
}
.txt-list-wrapper{
    border-radius: 10px;
    height: 197px;
}
.txt-list {
    border-radius: 10px;
    height: 110px;
    li {
        border-bottom: 1px solid $tertiary-light-color;
        padding: 8px 0;
    }
}
.txt-btn-list {
    li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include pad {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    .txt-btn-list-txt {
        width: calc(100% - 158px);
        @include pad {
            margin-bottom: 10px;
            width: 100%;
        }
        &-en {
            width: calc(100% - 295px);
            @include PC {
                margin-bottom: 10px;
                width: 100%;
            }
        }
    }
}
.tag-list {
    li {
        display: flex;
        align-items: flex-start;
        &:last-child {
            border-bottom: none;
        }
        .tag-list-tag {
            background: $primary-xlight-color;
            color: $gray700;
            padding: 0 8px;
            border-radius: 5px;
            margin-right: 15px;
            font-size: $font-sm;
            text-align: center;
            flex: 0 0 90px;
        }
    }
}
.tag-list-header {
    border-radius: 30px 30px 0 0;
    background: $gray500;
    margin-bottom: 0;
    padding: 10px;
}
.list-group-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-left-width: 5px !important;
    @include mobile-md{
        flex-direction: column;
        align-items: start;
        &.list-group-header{
            display: none;
        }
    }
    &:hover {
        background: $secondary-color;
    }
}
.list-badge{
    border-left: 1px solid $gray500;
    padding: 0 0 0 20px;
    color: $secondary-color;
    @include pad{
        padding: 0 0 0 10px;
    }
}
.disabled {
    pointer-events: none !important;
}
/*-----------------區塊樣式---------------------*/
.info-box {
    background: $white;
    border-radius: 8px;
    border: 1px solid $primary-color;
    border-left: 6px solid $primary-color;
    border-right: 6px solid $primary-color;
    padding: 20px;
}
.notice-box{
    display: flex;
    justify-content: center;
    padding: 32px 0;
    align-items: center;
    font-size: $font-md;
    @include mobile-md{
        font-size: $font-sm;
    }
    &[data-content="export-no-entry"]{
        border: 2px dashed $primary-weak-color;
        border-radius: 5px;
    }
    &[data-content="export-none"]{
        border: 1px solid $gray200;
        border-radius: 5px;
    }
    &[data-content="search-no-info"]{
        height: calc(100% - 68px);
    }
}

/*---------------------表格樣式---------------------*/
@each $key, $value in $color-themes {
    //文字顏色
    .table-bd-#{$key} {
        th,
        td {
            border-color: $value;
        }
    }
}
.modal{
    .table-wrapper{
        padding: 0;
        height: 530px;
        thead{
            position: sticky;
            top: 0;
            z-index: 1;
        }
        td{
            height: 50px;
        }
    }
}
.table{
    color: $gray700;
    font-size: $font-xs;
    &>tbody>tr:nth-child(even){
        background: $tertiary-xxlight-color;
    }
    th{
        background: $primary-xlight-color;
        color: $secondary-xdark-color;
        font-weight: $font-light;
        text-align: center;
        position:sticky
    }
    td{
        text-align: center; 
        vertical-align: middle;
    }
    &>:not(:first-child){
        border-top: none;
    }
    .btn{
        opacity: 0.8;
        &:hover{
            opacity: 1;
        }
    }
}
.table-rwd {
    min-width: 100%;
    th {
        display: table-cell;
        border-color: $white;
        @include pad {
            display: none;
        }
    }
    td {
        display: table-cell;
        border-color: $gray500;
        @include pad {
            display: flex;
            flex-direction: column;
            text-align: left;
            display: block;
            border: 0 !important;
            padding: 10px 20px 10px 20px !important;
            position: relative;
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: calc(100% - 40px);
                height: 1px;
                left: 20px;
                bottom: 0;
                background: $gray500;
            }
            &.table-head:first-child {
                background: $gray500;
                color: $white;
                font-weight: $font-light;
                &:before {
                    display: inline-block;
                    color: $white;
                    background: none;
                    padding: 0;
                    font-weight: $font-light;
                }
            }
            &:last-child {
                &:after {
                    display: none;
                }
            }
        }
        &:before {
            display: none;
            @include pad {
                content: attr(data-th);
                display: inline-block;
                padding: 2px 10px;
                color: $white;
                font-size: $font-xs;
                background: lighten($gray500, 15%);
                border-radius: 15px;
            }
        }
    }
}
.table-link {
    display: block;
    width: calc(100% + 1.5em);
    height: calc(100% + 1.5em);
    margin: -0.75em;
    padding: 0.75em;
    color: $gray700;
    border: 1px solid transparent;
    position: relative;
    &:after {
        position: absolute;
        content: "";
        width: 18px;
        height: 18px;
        background: url(../images/icon-plus.svg) no-repeat center;
        background-size: contain;
        right: 5px;
        bottom: 5px;
        opacity: 0.4;
    }
    &:hover {
        border: 1px solid $primary-color;
        background: $white;
        color: $gray700;
    }
}
.table-height {
    //讓td內容器可以高100%
    td {
        height: 1px;
    }
}
.table-item{
    display: inline-block;
    background: $white;
    border-radius: 4px;
    padding: 3px 2px 3px 10px;
    border: 1px solid $primary-color;
    .btn{
        padding: 5px;
        font-size: $font-xs;
        color: $danger-color;
        opacity: 0.8;
        &:hover{
            opacity: 1;
        }
        img{
            width: 10px;
        }
    }
}
.table-form{
    .table-label{
        background: $gray500;
        color: $white!important;
    }
    .table-sub-label{
        font-size: $font-xxs;
        display: block;
        color: darken($color: $pale-color, $amount: 10%);
    }
    .form-control,.form-select{
        font-size: $font-sm;
    }
    td{
        vertical-align: middle;
    }
}
/*---------------------捲軸樣式---------------------*/
.scroll {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $gray300;
    }
    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        background-color: $gray300;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $gray500;
    }
}
.scroll-light {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $white;
        border: 1px solid $gray300;
        border-radius: 10px;
    }
    &::-webkit-scrollbar {
        width: 6px;
        height: 6px;
        background-color: $white;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: lighten($color: $gray300, $amount: 15%);
    }
}
.scroll-border {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $tertiary-color;
        border-radius: 15px;
    }
    &::-webkit-scrollbar {
        width: 6px;
        height: 6px;
        background-color: $white;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $white;
        border: 1px solid $tertiary-color;
    }
}
.scrollx-none {
    overflow-x: hidden !important;
}
.scrolly-none {
    overflow-y: hidden !important;
}

.item_text {
    display: inline-block;
    background: $secondary-color;
    color: lighten($color: $primary-color, $amount: 15%);
    padding: 0px 10px;
    margin: 5px 10px 5px 0;
    border-radius: 5px;
}

.info-line {
    //線條小標
    display: flex;
    justify-content: space-between;
    position: relative;
    &::after {
        content: "";
        display: block;
        position: absolute;
        background: $gray700;
        width: 100%;
        height: 1px;
        left: 0;
        top: 12px;
        z-index: 50;
    }
    span {
        background: $white;
        position: relative;
        z-index: 100;
        padding: 0 5px;
    }
}
.article-title {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid $gray700;
    font-weight: $font-bold;
    padding-bottom: 15px;
    flex-wrap: wrap;
    @include PC {
        flex-direction: column-reverse;
        font-size: $font-xl;
    }
    @include PC {
        font-size: $font-lg;
        font-weight: $font-heavy;
        padding-bottom: 15px;
    }
}
.article-text {
    line-height: 1.8;
    margin-bottom: 35px;
}

.line-higher {
    line-height: 1.8;
    @include mobile-lg {
        line-height: 1.5;
    }
}
.map-title {
    background: lighten($color: $secondary-color, $amount: 25%);
    color: $primary-color;
    @include PC {
        font-size: $font-md;
    }
}
.media-title {
    @include mobile-lg {
        font-size: $font-xs;
    }
}
.basic-title {
    font-size: $font-sm;
    font-weight: $font-heavy;
}
/*-----------------列表樣式---------------------*/
.img-list{
    display: flex;
    flex: nowrap;
    padding-bottom: 16px;
    &-item{
        margin-right: 16px;
    }
    &-link{
        display: block;
        width: 145px;
        height: 145px;
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &-pic{
        width: 140px;
        height: 140px;
        border-radius: 10px;
        overflow: hidden;
        position: relative;
        img{
            height: 100%;
        }
    }
    &-btn{
        position: absolute;
        right: 8px;
        bottom: 8px;
        width: 28px;
        height: 28px;
        border-radius: 50%;
        background: $white;
        z-index: 10;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        &:hover{
            background: $danger-color;
            img{
                filter: invert(100%) sepia(100%) saturate(2%) hue-rotate(320deg) brightness(500%) contrast(101%);
            }
        }
        img{
            width: 20px;
        }
    }
    &-add{
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        border-radius: 10px;
        border: 2px dashed $primary-light-color;
        color: $primary-weak-color;
        font-size: $font-xs;
        height: 90px;
        &:hover{
            color: $primary-color;
            border: 2px dashed $primary-color;
            img{
                filter: invert(50%) sepia(79%) saturate(1675%) hue-rotate(185deg) brightness(100%) contrast(88%);
            }
        }
    }
}
.icon-list {
    border-top: 1px solid $gray700;
    .icon-list-item {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid $gray700;
        padding: 8px 0;
        p {
            display: flex;
        }
        img {
            width: 30px;
            margin-right: 15px;
        }
    }
}
.news-list {
    border-top: 1px solid $gray700;
    .news-list-item {
        border-bottom: 1px solid $gray700;
        a {
            width: 100%;
            padding: 19px 0;
            display: flex;
        }
        P {
            width: calc(100% - 110px);
            @include over-text(1);
        }
    }
    .news-list-date {
        color: $primary-color;
        width: 110px;
    }
}
.mark-menu {
    border-bottom: 1px solid lighten($color: $secondary-color, $amount: 10%);
    margin-bottom: 25px;
    .mark-menu-item {
        border-right: 1px solid lighten($color: $secondary-color, $amount: 10%);
        &:first-child {
            a {
                border-radius: 8px 0 0 0;
            }
        }
        &:last-child {
            border-right: none;
            a {
                border-radius: 0 5px 0 0;
            }
        }
    }
    a {
        display: block;
        width: 100%;
        background: lighten($color: $secondary-color, $amount: 15%);
        color: $gray700;
        position: relative;
        padding: 13px 25px;
        font-size: $font-sm;
        &.active {
            background: $white;
            font-weight: $font-heavy;
            &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 3px;
                background: $primary-color;
                left: 0;
                bottom: 0;
            }
        }
    }
}
.article-menu {
    border-bottom: 7px solid $tertiary-color;
    a {
        font-weight: $font-bold;
        text-align: center;
        min-width: 140px;
        padding-left: 0;
        padding-right: 0;
    }
}
.text-list {
    padding-left: 25px;
    li {
        list-style-type: decimal;
    }
}
.step-list {
    display: flex;
    flex-wrap: wrap;
    li:nth-child(even) {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 10px;
    }
    .step-item {
        border: 1px solid $secondary-color;
        padding: 5px 20px;
        @include mobile-lg {
            padding: 4px 13px;
            font-size: $font-xs;
        }
    }
}
.align-btn-list {
    .align-btn {
        width: 100%;
        padding-left: 30%;
        @include PC {
            padding-left: 25%;
        }
        @include mobile-md {
            padding-left: 15%;
        }
        &:hover {
            img {
                -webkit-filter: invert(200%) sepia(4%) saturate(2%) hue-rotate(173deg) brightness(700%) contrast(100%);
                filter: invert(200%) sepia(4%) saturate(2%) hue-rotate(173deg) brightness(700%) contrast(100%);
            }
        }
    }
}
.basic-list {
    @include mobile-md {
        font-size: $font-xs;
        letter-spacing: normal;
    }
}

/* ---------------------頁數開始------------------------*/

.topic-hot-tag {
    li {
        display: inline-block;
        width: 180px;
        max-width: 180px;
        padding: 0 5px;
        margin: 0 0 10px 0;
        @include PC-md {
            width: 33.33%;
        }
        @include pad {
            width: 50%;
        }
        a {
            display: block;
            width: 100%;
            @include pad {
                padding: 5px 7px;
                font-size: $font-xxs;
                text-align: center;
            }
            span {
                color: $primary-color;
            }
            &:hover {
                span {
                    color: $white;
                }
            }
        }
        .topic-hot-tag-now {
            span {
                color: $white;
            }
        }
    }
    &-sm {
        li {
            width: 150px;
            max-width: 150px;
            @include PC-md {
                width: 33.33%;
            }
            @include pad {
                width: 50%;
            }
        }
        a {
            text-align: center;
        }
    }
}
.topic-hot-link {
    li {
        display: inline-block;
    }
    a {
        font-size: $font-xs;
        @include pad {
            font-size: $font-xxs;
        }
    }
}
/*-----------------區塊樣式---------------------*/
.pagination{
    .page-item{
        padding: 0 2px;
    }
    .page-link{
        border-radius: 3px;
        background: lighten($gray500,25%);
        color: $white;
        padding: 0;
        width: 28px;
        height: 28px;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            width: 15px;
        }
    }
}
.page-item.disabled .page-link{
    background: lighten($gray500,35%);
}
.page-item.active .page-link{
    background: $assist-color;
    border-color: $assist-color;
}
.event-card {
    box-shadow: 0 0 5px $gray300;
    padding: 18px;
    &:hover {
        img {
            opacity: 0.5;
            transform: perspective(250px) translateZ(50px);
        }
        .event-card-img-box-text {
            opacity: 1;
            z-index: 100;
        }
    }
    .event-card-img-box-text {
        transition: all 0.2s ease;
        color: $white;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        padding: 30px;
        pointer-events: none;
        p {
            position: absolute;
            width: 100%;
            left: 0;
            bottom: 25%;
            font-size: $font-xs;
            font-weight: $font-light;
            @include over_text(3);
            padding: 0 10%;
        }
    }
    .event-card-img-box {
        display: block;
        background: $gray700;
        position: relative;
        overflow: hidden;
        img {
            transition: all 0.2s ease;
        }
    }
    .event-card-title {
        font-size: $font-md;
        @include over_text(2);
    }
    .event-card-action {
        display: flex;
        justify-content: space-between;
        & > * {
            padding: 5px 25px;
        }
    }
    .event-card-info {
        @include mobile-lg {
            font-size: $font-xs;
            letter-spacing: normal;
        }
        span {
            color: $primary-color;
            font-weight: $font-bold;
            margin-right: 10px;
        }
    }
}
.news-card {
    padding: 30px;
    border: 1px solid $gray300;
    @include pad {
        padding: 20px;
    }
    &:hover {
        background: $white;
    }
    .news-card-title {
        font-size: $font-lg;
        font-weight: $font-heavy;
        line-height: 1.4;
        @include over_text(2);
        @include pad {
            font-size: $font-md;
        }
    }
    .news-card-subtitle {
        font-size: $font-md;
        font-weight: $font-heavy;
        @include pad {
            font-size: $font-sm;
        }
        @include mobile-lg {
            font-size: $font-xs;
        }
    }
    .news-card-info {
        display: flex;
        justify-content: space-between;
    }
    .news-card-txt {
        p {
            @include over_text(4);
            @include PC-md {
                @include over_text(3);
            }
            @include PC {
                @include over_text(2);
                font-size: $font-xs;
            }
        }
    }
}
.album {
    @include PC {
        height: auto;
    }
    .row {
        height: 100%;
    }
    .album-logo {
        width: 150px;
        @include pad {
            margin: 15px auto;
        }
    }
    .album-box {
        display: block;
        position: relative;
        &:hover {
            opacity: 0.8;
        }
        .album-icon {
            position: absolute;
            right: 15px;
            bottom: 15px;
            font-size: $font-xl;
        }
    }
}
.topic-card {
    border-bottom: 1px solid $gray700;
    &-round {
        border-radius: 8px;
        padding: 18px;
        border: 1px solid $gray300;
    }
    .topic-title {
        font-size: $font-md;
    }
    .topic-subtitle {
        font-size: $font-sm;
    }
    .topic-mark-box {
        display: flex;
        flex-wrap: wrap;
    }
    .topic-mark {
        color: $white;
        padding: 2px 4px;
        font-size: $font-xxs;
        text-align: center;
        margin-right: 1px;
        font-weight: $font-light;
    }
    .topic-hot-tag {
        font-size: $font-xs;
        li {
            @include mobile-lg {
                width: auto;
                margin: 0 2% 10px 2%;
            }
        }
    }
    .topic-info {
        font-weight: $font-light;
        @include over-text(2);
    }
}
.favorite {
    display: block;
    width: 20px;
    height: 19px;
    @include text-indent;
    &-lg {
        font-size: $font-lg;
    }
    &-line {
        background: url(../images/icon-heart-line.svg) no-repeat center;
        background-size: 100%;
    }
    &-solid {
        background: url(../images/icon-heart-solid.svg) no-repeat center;
        background-size: 100%;
    }
    &-delete {
        background: url(../images/icon-heart-solid-primary.svg) no-repeat center;
        background-size: 100%;
    }
}
.share {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include PC {
        width: 100%;
        margin-bottom: 15px;
    }
    a {
        display: block;
        width: 30px;
        height: 30px;
    }
    .favorite {
        height: 28px;
        & + span {
            font-size: $font-xxxs;
        }
    }
}
.mark-box {
    .mark-box-menu {
        display: flex;
        li:first-child {
            a {
                border-radius: 5px 5px 0 0;
            }
        }
        li {
            a {
                display: block;
                padding: 10px 30px 10px 30px;
                background: lighten($color: $secondary-color, $amount: 15%);
                color: $gray700;
                border-radius: 0 5px 0 0;
                font-weight: $font-bold;
                margin-top: 4px;
                @include pad {
                    padding: 8px 20px 8px 20px;
                }
                @include mobile-lg {
                    padding: 8px 10px 8px 10px;
                    font-size: $font-xs;
                }
                &:hover {
                    color: $white;
                }
                &.active {
                    border-radius: 5px 5px 0 0;
                    background: $white;
                    padding: 14px 30px 10px 30px;
                    box-shadow: 0 -2px 3px $gray300;
                    margin-top: 0;
                    color: $secondary-color;
                    @include pad {
                        padding: 12px 20px 8px 20px;
                    }
                    @include mobile-lg {
                        padding: 12px 10px 8px 10px;
                    }
                    &:hover {
                        color: $secondary-color;
                    }
                }
            }
        }
    }
    .mark-box-content {
        background: $white;
        border-radius: 0 8px 8px 8px;
        box-shadow: 0 0 4px $gray300;
        padding: 40px 8%;
    }
    label {
        font-weight: $font-bold;
    }
}
.slide-box-img {
    height: 550px;
    overflow: hidden;
    @include pad {
        height: auto;
    }
}
.box-content {
    background: $white;
    border-radius: 8px;
    box-shadow: 0 0 4px $gray300;
    padding: 0 8%;
}
.info-form {
    label,
    .label {
        width: 110px;
        font-weight: bold;
        justify-content: flex-start;
        @include pad {
            width: 100%;
        }
    }
    .custom-control-label {
        width: auto;
    }
    input,
    select,
    textarea,
    .form-notice {
        width: calc(100% - 110px) !important;
        @include pad {
            width: 100% !important;
        }
    }
    .form-check-input,
    .form-check-label {
        width: auto !important;
        font-weight: $font-bold;
    }
    .custom-control-label {
        font-weight: normal;
    }

    .custom-select {
        background: url(../images/icon-arrow-down-secondary.svg) no-repeat right top $white;
        &-short {
            width: 230px !important;
            @include PC {
                width: 160px !important;
            }
            @include pad {
                width: 100% !important;
            }
        }
    }
    .form-control[readonly] {
        background-color: $white;
    }
}

.form-text-box {
    height: 200px;
    border: 1px solid lighten($color: $gray300, $amount: 15%);
    @include mobile-lg {
        height: 300px;
    }
}
.form-notice {
    margin-left: 130px;
    @include pad {
        margin-left: 0;
    }
}
.mobile-rounded {
    @include PC {
        border-radius: 10px;
    }
}
/*-----------------表單樣式---------------------*/
.btn-input-group{
    input{
        border-radius: 50rem;
        padding-left: 20px;
    }
    button{
      border-radius: 0 50rem 50rem 0 !important;
      background: $white;
      border-left: 0;
    }
}
.inside-bar {
    //input內置按鈕
    button {
        padding: 5px;
        border: 1px solid $secondary-color;
        border-radius: 4px;
        width: 35px;
    }
}
.inside-bar-round {
    //input內置按鈕大圓角
    input {
        border-radius: 70px 0 0 70px;
    }
    button {
        padding: 3px 14px;
        border-radius: 0 70px 70px 0;
        width: 50px;
        height: 38px;
    }
}
.input-over-light-base,
.input-over-light-base:focus {
    background-color: lighten($color: $secondary-color, $amount: 20%) !important;
    border-radius: 0;
    border: 0;
}
.custom-select {
    background: url(../images/icon-arrow-down.svg) no-repeat right top $white;
}
.round {
    //大圓角
    border-radius: 70px;
}
.fortime {
    background: url(../images/icon-time.svg) no-repeat right 2px $white !important;
    background-size: 50px;
    padding-right: 50px !important;
}
.fortime-secondary {
    background-image: url(../images/icon-time-secondary.svg);
    background-repeat: no-repeat;
    background-position: right top;
    background-size: 50px;
    padding-right: 50px !important;
}
.steady-checkbox {
    .custom-control-label::before {
        border: 1px solid $tertiary-color;
        border-radius: 0;
    }
    .custom-control-input:checked ~ .custom-control-label::after {
        border: 1px solid $tertiary-color;
        background-color: $tertiary-color;
    }
}
.range-group{
    .progress{
        background: 0;
        height: 25px;
        position: relative;
    }
    .progress-bar{
        position: absolute;
        left: 0;
        top: 8px;
        height: 7px;
        background: $quaternary-color;
        border-radius: 50px;
    }
    .range-number{
        width: 60px;
        flex-shrink: 0;
        text-align: center;
    }
}

/*-----------------文字樣式---------------------*/
.small-txt {
    color: $gray500;
    font-size: $font-xs;
}
.pattern-bg {
    background: url(../images/bg-left.png) no-repeat, url(../images/bg-right.png) no-repeat;
    background-position: left top, right top;
    background-size: 20%, 30%;
}
.letter-spacing-mobile-n {
    @include mobile-lg {
        letter-spacing: normal;
    }
}

/*-----------------連結樣式---------------------*/
.img-link {
    //圖片按鈕
    opacity: 1;
    transition: all 0.2s ease;
    &:hover {
        opacity: 0.8;
    }
}
.shadow-link {
    &:hover {
        box-shadow: none !important;
        transition: all 0.2s ease;
    }
}
.article-menu-select {
    div {
        background: $tertiary-color;
        color: $white;
        height: 38px;
        line-height: 38px;
        padding: 0 20px;
    }
    select {
        border: 1px solid $tertiary-color;
        border-radius: 0;
        min-width: 200px;
    }
}
/*-----------------flex樣式---------------------*/
.flex-unstrech {
    flex: 0 1 auto;
}
