@charset "utf-8";
@import url("https://fonts.googleapis.com/earlyaccess/notosanstc.css");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap");
@import url("../webfonts/glyphicons-halflings-regular.ttf");

/* --------------基本設定開始--------------- */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	width: 100%;
	-webkit-text-size-adjust: none;
}
body {
	-webkit-font-smoothing: antialiased;
	-webkit-overflow-scrolling: touch;
	font-family: "Noto Sans TC", "Microsoft JhengHei", "sans-serif";
	line-height: 1.5;
	letter-spacing: 1px;
	color: $gray800;
	font-size: 1.1em; //字級加大
}
/* --------------字級加大結束--------------- */
.clear {
	clear: both;
	height: 0;
	font-size: 1px;
	line-height: 0px;
}
img {
	display: block;
	max-width: 100%;
	height: auto;
}
a,
a:hover,
button,
button:hover {
	transition: all 0.2s ease;
	text-decoration: none;
}
a:focus {
	outline: none;
}
p {
	margin-bottom: 0;
}
ul {
	padding-left: 0;
	list-style: none;
	margin-bottom: 0;
}
ol {
	margin-bottom: 0;
}

.form-control::placeholder {
	color: $gray600;
	font-size: $font-xsm;
}
.form-control:disabled{
	background: $gray100;
	border: 1px solid $gray100;
}
.form-check-input:checked {
	background: $quaternary-color;
	border: 1px solid $quaternary-color;
}
.form-label {
	color: $secondary-xdark-color;
	font-size: $font-sm;
}
.form-select,
.form-control {
	border: 1px solid $quaternary-light-color;
	color: $gray800;
	border-radius: 10px;
	font-size: $font-xsm;
}
.form-select {
	background: url("../images/ic-dropdown.svg") no-repeat right center $white;
}
.form-control {
	&[readonly] {
		background: lighten($color: $primary-xxlight-color, $amount: 3%);
		border: 1px solid $gray200;
		box-shadow: none;
	}
}
.input-group{
	.input-group-append{
		.input-group-text{
			border: 1px solid $quaternary-light-color;
			border-radius: 0 10px 10px 0;
		}
	}
	&>.input-group-text{
		height: 36px;
		border: 1px solid $quaternary-color;
		color: $quaternary-color;
		border-radius: 0 10px 10px 0;
	}
	&>[disabled]+.input-group-text{
		height: 36px;
		border: 1px solid $gray200;
		margin-left: 0 !important;
		background: $gray100;
		color: $gray600;
		border-left: 1px solid $gray600;
		border-radius: 0 10px 10px 0;
	}
}
/* 更改滑塊的顏色 */
.form-range::-webkit-slider-thumb {
	background-color: $quaternary-color;
}
.form-range::-moz-range-thumb {
	background-color: $quaternary-color;
}
.form-range::-ms-thumb {
	background-color: $quaternary-color;
}

.input-group-text {
	background: $white;
	border: 1px solid $primary-light-color;
	border-radius: 0 3px 3px 0;
	height: 100%;
	padding: 0 8px;
}
.datetimepicker-input {
	padding-right: 0;
}
.btn {
	border-radius: 10px;
	&:focus {
		box-shadow: none;
	}
}
.btn-sm {
	border-radius: 5px !important;
}
.btn-lg{
	font-size: $font-sm;;
	padding: 12px 0;
}
.container {
	@include PC-sm {
		max-width: 800px;
	}
}
.modal {
	&-title {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		color: $secondary-xdark-color;
		font-size: $font-lg;
		font-weight: $font-heavy;
		@include pad{
			font-size: $font-md;
		}
		@include mobile-lg {
			width: 100%;
			font-size: $font-sm;
		}
		img {
			width: 24px;
			margin-right: 8px;
		}
	}
	&-subtitle{
		display: flex;
		align-items: center;
		border-bottom: 1px solid $tertiary-light-color;
		padding-bottom: 8px;
		margin-bottom: 12px;
		color: $secondary-dark-color;
		font-size: $font-smd;
		@include pad{
			font-size: $font-sm;
			img{
				width: 20px;
			}
		}
	}
	&-subsubtitle{
		display: flex;
		padding-bottom: 8px;
		align-items: flex-start;
		color: $quaternary-light-color;
		font-size: $font-sm;
		img{
			width: 20px;
		}
		button{
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background: $white;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
		margin-top: -5px;
        &:hover{
            background: $danger-color;
            img{
                filter: invert(100%) sepia(100%) saturate(2%) hue-rotate(320deg) brightness(500%) contrast(101%);
            }
        }
        img{
            width: 24px;
        }
		}
	}
	&-tab-title{
		font-size: $font-md;
		font-weight: $font-heavy;
		text-align: center;
		width: 100%;
		@include pad{
			font-size: $font-sm;
		}
		@include mobile-lg{
			font-size: $font-sm;
		}
		span{
			font-size: $font-sm;
			text-align: center;
			font-weight: $font-light;
			@include pad{
				font-size: $font-xxs;
			}
		}
	}
	&-form-group{
		display: flex;
		align-items: center;
		@include mobile-lg{
			flex-wrap: wrap;
		}
	}
	&-tab-txt{
		height: 350px;
		label{
			color: $secondary-xdark-color !important;
			flex-shrink: 0;
			text-align: right;
			padding: 0 8px;
			@include mobile-lg{
				width: 100% !important;
				text-align: left;
				padding: 0;
				margin-bottom: 4px;
			}
		}
	}
	&-tab-list{
		list-style-type: disc;
		padding-left: 20px;
	}
	&-tab-btn{
		min-width: 120px;
		border-radius: 5px !important;
		@include pad{
			font-size: $font-xs;
		}
		@include mobile-md{
			width: 100%;
		}
	}
	&-spline{
		background: $tertiary-light-color !important;
		opacity: 1;
		margin: 24px 0;
	}
}
.modal-content {
	overflow: hidden;
	position: relative;
	.btn-close {
		background: url("../images/ic-cross.svg") center;
		background-size: 24px;
		opacity: 1;
		position: absolute;
		right: 10px;
		top: 11px;
		&:hover {
			opacity: 0.7;
		}
	}
}
.modal-header {
	padding: 10px 24px;
	justify-content: space-between;
	@include mobile-lg {
		padding: 10px 12px;
		flex-direction: column-reverse;
	}
}
.modal-body {
	font-size: $font-xs;
	label {
		color: $tertiary-color;
	}
	.modal-tabs{
		border-bottom: 1px solid $secondary-xdark-color;
		flex-wrap: nowrap;
	}
	.nav-link {
		font-size: $font-sm;
		color: $secondary-xdark-color;
		padding: 8px 32px;
		flex-shrink: 0;
		border: 0;
		@include PC{
			padding: 8px 24px;
		}
		@include pad{
			padding: 8px 16px;
			font-size: $font-xsm;
		}
		&:hover{
			border-bottom: 0;
		}
		&.active,&:focus {
			color: $secondary-dark-color;
			position: relative;
			&:after{
				content:"";
				position: absolute;
				left: 0;
				bottom: 0;
				width: 100%;
				height: 5px;
				background: $secondary-color;
			}
		}
	}
}
.modal-lg {
	max-width: 1080px;
}
.modal-xl {
	max-width: 1500px;
}
iframe{
	width: 100%;
}
