/*--------------------選單開始------------------*/
.navbar {
	transition: all 0.2s ease;
	padding-top: 0;
	padding-bottom: 0;
	&-brand {
		width: 159px;
		@include mobile-lg {
			width: 130px;
		}
	}
	&-nav {
		display: flex;
		align-items: center;
		@include PC {
			width: 100%;
		}
	}
	&-toggler {
		display: flex;
		flex-direction: column;
		align-items: center;
		color: $secondary-xdark-color;
		border: 0;
		border-radius: 50rem;
		font-size: $font-sm;
		padding: 8px !important;
		min-width: 72px;
		font-size: $font-xxs;
		@include mobile-md {
			min-width: auto;
		}
		&:hover {
			background: rgba(255, 255, 255, 1);
		}
		&:focus {
			box-shadow: none;
		}
	}
	.btn-input-group {
		@include PC {
			width: 200px;
		}
		@include pad {
			width: 100%;
			max-width: 300px;
			margin: 0 auto;
		}
	}
	.nav-item {
		@include PC {
			width: 100%;
			display: flex;
			justify-content: center;
			border-bottom: 1px solid $secondary-color;
			flex-wrap: wrap;
		}
	}
	.nav-link {
		padding: 12px 8px;
		min-width: 72px;
		color: $secondary-xdark-color;
		font-size: $font-xxs;
		display: flex;
		flex-direction: column;
		align-items: center;
		border-radius: 0 !important;
		&[data-style="dark"] {
			color: $white;
			background: $secondary-color;
		}
		img {
			width: 30px;
		}
		&:hover {
			opacity: 0.8;
		}
		@include PC {
			display: flex;
			color: $white;
			padding: 10px 0;
			width: 100%;
			background: $secondary-xdark-color !important;
			justify-content: center;
			flex-direction: row;
			&:hover {
				background: darken($color: $secondary-xdark-color, $amount: 10%) !important;
				opacity: 1;
				color: $white;
			}
			img {
				margin-right: 10px;
				width: 24px;
			}
		}
	}
	.nav-dropdown {
		position: absolute;
		right: 0;
		top: 73px;
		width: 230px;
		background: $secondary-xdark-color;
		color: $white;
		@include PC {
			position: relative;
			right: auto;
			top: auto;
			width: 100%;
			background: $tertiary-xlight-color;
			color: $secondary-xdark-color;
		}
		&:before {
			content: "";
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 0 10px 10px 10px;
			border-color: transparent transparent $secondary-xdark-color transparent;
			position: absolute;
			top: -10px;
			right: 98px;
		}
		&-title {
			font-size: $font-md;
			text-align: center;
			padding: 12px 16px;
			margin-bottom: 0;
			@include PC {
				font-size: $font-sm;
			}
		}
		&-info {
			padding: 16px 24px;
			li {
				display: flex;
				font-size: $font-xs;
				padding: 4px 0;
				align-items: start;
				line-height: 1.3;
				img {
					margin-top: -2px;
				}
			}
			p {
				display: inline-block;
				width: calc(100% - 30px);
				word-break: break-all;
			}
		}
		&-list {
			a {
				font-size: $font-xsm;
				display: flex;
				justify-content: center;
				color: $white;
				padding: 12px 16px;
				border-bottom: 1px solid $white;
				@include PC {
					background: $tertiary-xlight-color !important;
					color: $secondary-xdark-color !important;
				}
			}
			li:last-child {
				a {
					border-bottom: 0;
				}
			}
		}
	}
}

.map-menu {
	&-link {
		display: flex;
		color: $secondary-xdark-color;
		padding: 12px 16px;
		font-size: $font-xsm;
		border-bottom: 1px solid $tertiary-xlight-color;
		background: $white;
		img {
			width: 20px;
			margin-right: 4px;
		}
	}
}
.map-result {
	position: absolute;
	width: 100%;
	max-width: 430px;
	min-height: 42px;
	right: 0;
	top: 0;
	z-index: 100;
	&-btn {
		padding: 8px 24px;
		background: $tertiary-xlight-color;
		width: 100%;
		border-radius: 0 !important;
		color: $secondary-xdark-color;
		position: relative;
		img {
			position: absolute;
			right: 10px;
			top: calc(50% - 5px);
			transition: all 0.2s ease;
		}
		&:not(.collapsed){
			img{
				transform: rotate(180deg);
			}
		}
	}
	&-tab {
		width: 50%;
		background: $tertiary-light-color !important;
		color: $secondary-xdark-color;
		flex-direction: row;
		justify-content: center;
		border: 0 !important;
		font-size: $font-sm;
		padding: 8px 0;
		display: flex;
		border-top-left-radius: 0 !important;
		border-top-right-radius: 0 !important;
		img{
			width: 22px;
		}
		&:hover {
			opacity: 1;
		}
		&.active {
			color: $quaternary-color !important;
			background: $white !important;
			img {
				filter: invert(49%) sepia(83%) saturate(389%) hue-rotate(43deg) brightness(92%) contrast(101%);
			}
			&[data-style="primary"] {
				color: $primary-dark-color !important;
				img {
					filter: invert(41%) sepia(99%) saturate(5935%) hue-rotate(189deg) brightness(91%) contrast(101%);
				}
			}
		}
	}
	&-content{
		max-height: calc(100vh - 155px);
	}
	&-noinfo{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		font-size: $font-sm;
		text-align: center;
		padding: 48px 18px;
	}
	&-card {
		width: 100% !important;
		box-shadow: none;
		padding: 4px 16px 16px 16px;
		.btn-close {
			width: 10px;
			background-size: 100%;
			opacity: 1;
		}
		&-header {
			display: flex;
			align-items: center;
			.badge {
				border: 1px solid;
				margin-left: auto;
				vertical-align: middle;
				&-line {
					width: 1px;
					height: 10px;
					display: inline-block;
					margin: 0 8px;
				}
			}
		}
		&-title {
			display: flex;
			align-items: center;
			font-size: $font-smd;
			color: $gray800;
			margin-bottom: 0;
			span {
				position: relative;
				border-radius: 3px;
				padding: 4px 12px;
				color: $white;
				margin-right: 8px;
				font-weight: $font-heavy;
				img {
					position: absolute;
					left: calc(50% - 9px);
					bottom: -5px;
				}
			}
		}
		&-subtitle {
			display: flex;
			font-size: $font-xsm;
		}
		&-body {
			input {
				border-radius: 0 !important;
				border: none;
				height: 27px;
				font-size: $font-xs;
			}
			label {
				text-align: right;
				padding: 0 8px 0 0;
				font-size: $font-xs;
				flex-shrink: 0;
			}
		}
		&-list {
			&-item {
				background: $white;
				display: flex;
				margin-bottom: 2px;
				p{
					flex-shrink: 0;
				}
				span {
					border-left: 1px solid;
				}
				& > * {
					padding: 4px 16px;
					display: inline-block;
					height: 100%;
					color: $gray800;
				}
			}
		}
		&-accordion {
			max-height: 140px;
			color: $gray800;
			.accordion-item{
				margin-bottom: 2px;
				border: 0;
			}
			.accordion-button {
				display: flex;
				justify-content: space-between;
				padding: 4px 16px;
				align-items: center;
				color: $gray800;
				font-size: $font-xs;
				&:focus{
					box-shadow: none;
				}
				&::after {
					content: "";
					display: block;
					width: 22px;
					height: 22px;
					background: url("../images/ic-plus.svg");
				}
			}
			.accordion-button:not(.collapsed) {
				background: $white;
				color: $secondary-dark-color;
				&::after{
					background: url("../images/ic-minus.svg");
				}
			}
		}
		&[data-style="quaternary"] {
			background: $quaternary-xlight-color;
			border-top: 6px solid $quaternary-light-color;
			.btn-close {
				background: url("../images/ic-close-green.svg");
			}
		}
		&[data-style="primary"] {
			background: $tertiary-xlight-color;
			border-top: 6px solid $primary-color;
			.btn-close {
				background: url("../images/ic-close-blue.svg");
			}
		}
	}
}
.map-dot {
	position: absolute;
	width: 40px;
	height: 35px;
	border-radius: 8px;
	border-width: 3px;
	border-style: solid;
	color: $white;
	font-size: $font-md;
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: $font-heavy;
	&:after {
		content: "";
		display: block;
		position: absolute;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 10px 8px 0 8px;
		left: calc(50% - 9px);
		bottom: -10px;
	}
	&[data-style="primary-dark"] {
		border-color: $primary-dark-color;
		background: $primary-dark-color;
		&::after {
			border-color: $primary-dark-color transparent transparent transparent;
		}
	}
	&[data-style="quaternary"] {
		border-color: $quaternary-color;
		background: $quaternary-color;
		&::after {
			border-color: $quaternary-color transparent transparent transparent;
		}
	}
}
/*------------------------footer開始----------------------------*/
.gotop {
	display: block;
	width: 57px;
	height: 37px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	right: 10px;
	bottom: 10px;
	padding: 0;
	background: url(../images/gotop.svg);
}
footer {
	.footer-logo {
		max-width: 100px;
		@include pad {
			max-width: 60px;
		}
	}
}

/*------------------------測邊選單開始----------------------------*/
.side-menu {
	margin-top: 73px;
	width: 350px;
	height: calc(100vh - 73px);
	position: fixed;
	left: 0;
	transition: all 0.2s ease;
	z-index: 200;
	background: $tertiary-xlight-color;
	box-shadow: 0 10px 10px $gray600;
	@include PC {
		width: 280px;
		padding-top: 16px;
		height: calc(100vh - 59px);
		margin-top: 59px;
	}
	@include pad {
		margin-top: 46px;
		min-height: calc(100vh - 46px);
	}
	&-body {
		height: 100%;
	}
	&-toggler {
		padding: 0;
		width: 26px;
		background: $white;
		height: 54px;
		border-radius: 0 8px 8px 0 !important;
		position: absolute;
		right: -26px;
		top: 30px;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		z-index: 10;
		box-shadow: 4px 4px 4px $gray600 !important;
	}
	&.close {
		left: -350px;
		@include PC {
			left: -280px;
		}
		.side-menu-toggler {
			img {
				transform: rotate(180deg);
			}
		}
	}
	.accordion-item {
		border-radius: 0 !important;
		border: 0 !important;
	}
	.accordion-button {
		background: $tertiary-xlight-color;
		font-size: $font-smd;
		padding: 16px 16px 16px 24px;
		color: $secondary-xdark-color;
		border-radius: 0 !important;
		position: relative;
		@include PC {
			padding: 8px 8px 8px 16px;
			font-size: $font-sm;
			margin-bottom: 8px;
		}
		@include pad {
			margin-bottom: 6px;
		}
		&::after {
			width: 24px;
			background: url("../images/ic-arrow.svg");
			background-size: contain;
		}
		&:not(.collapsed) {
			font-weight: $font-bold;
			color: $white;
			background: $secondary-color;
			box-shadow: none;
			@include PC {
				border-left: 5px solid $primary-color;
				padding: 8px 12px 8px 12px;
			}
			&::after {
				width: 24px;
				background: url("../images/ic-arrow-active.svg");
				background-size: contain;
			}
			img {
				filter: invert(48%) brightness(600%) contrast(100%);
			}
			.accordion-icon {
				transform: rotate(180deg);
			}
		}
		.accordion-icon {
			transition: all 0.2s ease;
			position: absolute;
			right: 24px;
			top: calc(50% - 5px);
		}
		&[data-icon="single-icon"] {
			padding: 8px 16px 8px 0;
			img {
				margin-right: 6px;
			}
			&:after {
				display: none;
			}
			&:not(.collapsed) {
				img {
					filter: invert(53%) sepia(17%) saturate(2256%) hue-rotate(43deg) brightness(97%) contrast(80%);
				}
			}
			& ~ .collapse {
				color: $secondary-xdark-color;
			}
		}
	}
	.accordion-sub {
		padding: 8px 16px 8px 30px;
		color: $secondary-xdark-color;
		background: none !important;
		box-shadow: none !important;
		font-size: $font-sm;
		&:after {
			position: absolute;
			height: 24px;
			left: 0;
			top: 8px;
			background: url("../images/ic-folder.svg") no-repeat;
		}
		&:not(.collapsed) {
			color: $quaternary-color;
			&:after {
				background: url("../images/ic-folder-close.svg") !important;
				transform: rotate(0deg);
			}
		}
	}
	.accordion-subsub {
		background: none !important;
		box-shadow: none !important;
		padding: 8px 16px 8px 40px;
		font-size: $font-sm;
		color: $secondary-xdark-color;
		&:after {
			position: absolute;
			height: 24px;
			left: 12px;
			top: 9px;
			background: url("../images/ic-circle-down.svg") center no-repeat;
		}
		&:not(.collapsed) {
			color: $secondary-xdark-color;
			&:after {
				background: url("../images/ic-circle-up.svg") center no-repeat;
			}
		}
		& ~ .accordion-collapse {
			padding-left: 40px;
		}
	}
	.accordion-body {
		background: $white;
		border-radius: 0 0 5px 5px;
		padding: 12px 24px;
		font-size: $font-sm;
		.input-group {
			width: calc(100% - 40px) !important;
		}
		&-title {
			color: $quaternary-color;
			border-bottom: 1px solid $quaternary-color;
			font-size: $font-md;
			font-weight: $font-heavy;
			padding: 8px 0;
		}
		hr {
			width: calc(100% + 48px);
			margin-left: -24px;
		}
	}
	.accordion-sub-body {
		font-size: $font-xs;
		position: relative;
		padding: 10px 15px 0 40px;
		&:after {
			content: "";
			display: block;
			height: 100%;
			width: 1px;
			background: $primary-light-color;
			position: absolute;
			left: 23px;
			top: 0;
		}
	}
}

.btn-upload {
	display: flex;
	justify-content: center;
	background: $quaternary-light-color;
	color: $white;
	border-radius: 5px !important;
	&:before {
		content: "";
		width: 24px;
		height: 24px;
		background: url("../images/ic-upload.svg");
		background-size: 100%;
		display: block;
		margin-right: 4px;
	}
	&:after {
		content: attr(data-con);
		display: inline;
		color: $white;
	}
	&:not(.collapsed) {
		background: $gray700;
		&:before {
			background: url("../images/ic-upload-off.svg");
			background-size: 100%;
		}
		&:after {
			content: "取消上傳";
		}
	}
}

.copyright {
	position: absolute;
	left: 0;
	bottom: 20px;
	font-size: $font-xxs;
	background: $white;
	padding: 10px 0;
	@include mobile-md {
		font-size: $font-xxxs;
		bottom: 10px;
	}
}
.side-menu-link {
	display: block;
	background: $pale-light-color;
	color: $gray700;
	padding-top: 10px;
	padding-bottom: 10px;
	&:hover {
		background: $white !important;
		color: $tertiary-color;
	}
}
.side-menu-item {
	width: 100%;
	&:hover {
		color: $primary-color;
	}
}
.side-menu-sub {
	display: flex;
	width: 100%;
	color: $primary-color;
	padding: 0 10px;
	&.collapsed {
		color: $gray700;
		.side-menu-icon {
			opacity: 0.8;
		}
	}
	&:focus {
		box-shadow: none;
	}
}
.side-menu-icon {
	background: $primary-color;
	border-radius: 50%;
	width: 26px;
	height: 26px;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0;
	opacity: 1;
	margin-right: 15px;
	img {
		width: 21px;
	}
}
/*------------------------內容區塊開始----------------------------*/
.side-content {
	width: calc(100% - 350px);
	height: calc(100vh - 73px);
	margin-left: 350px;
	margin-top: 73px;
	transition: all 0.2s ease;
	overflow: hidden;
	position: relative;
	display: flex;
	flex-direction: column;
	@include PC {
		width: calc(100% - 280px);
		height: calc(100vh - 59px);
		margin-top: 59px;
		margin-left: 280px;
	}
	@include pad {
		width: 100%;
		margin-left: 0;
	}
	&.close {
		width: 100%;
		margin-left: 0;
	}
}
/*------------------地圖區塊開始--------------------*/

.map-content-wrapper{
	height: calc(100vh - 73px);
	overflow: hidden;
	display: flex;
	transition: all 0.2s ease;
	@include PC {
		height: calc(100vh - 59px);
	}
	@include mobile-lg{
		flex-direction: column;
	}
	&.open {
		height: calc(100vh - 314px);
	}
	&.hide{
		height: 0 !important;
	}
	&.bigger{
		height: calc(100vh - 36px) !important;
	}
	&.compare{
		width: 50%;
	}
	&.ui-state-disabled {
		opacity: 1;
	}
	.toast-container{
		z-index: 100;
	}
}
.map-content{
	width: 100%;
	position: relative;
	flex-shrink: 0;
	transition: all 0.2s ease;
	&.compare{
		width: 50%;
		@include mobile-lg{
			width: 100%;
			height: 50%;
		}
	}
}
.map-compare{
	width: 100%;
	flex-shrink: 0;
	transition: all 0.2s ease;
	&.compare{
		width: 50%;
		display: block;
		@include mobile-lg{
			width: 100%;
			height: 50%;
		}
	}
}
/*------------------底部視窗開始--------------------*/
.side-info {
	width: 100%;
	height: 280px;
	flex: 1;
	display: none;
	position: relative;
	z-index: 100;
	transition: all 0.2s ease;
	&-header{
		display: flex;
		justify-content: space-between;
		align-items: center;
	}&-title{
		display: flex;
		align-items: center;
		img{
			filter: invert(51%) sepia(9%) saturate(3694%) hue-rotate(43deg) brightness(102%) contrast(89%);
		}
	}
	&-toggle{
		width: 100%;
		border: none !important;
		color: $quaternary-color !important;
		background: $quaternary-xlight-color !important;
		display: flex;
		align-items: center;
		justify-content: space-between;
		box-shadow: none !important;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 250;
		&>img{
			width: 20px;
			transition: all 0.2s ease;
		}
		&.collapsed{
			&>img{
				transform: rotate(180deg);
			}
		}
	}
	&-closer {
		position: absolute;
		right: 16px;
		top: 7px;
	}
	&-content-wrapper{
		margin-top: 36px;
	}
	&-tool{
		position: relative;
		&-con{
			display: flex;
		justify-content: center;
		border: 1px solid $primary-color;
		}
		&-btn{
			border-radius: 0 !important;
			background: $primary-color;
			height: 32px;
			&:not(.collapsed){
				background: $primary-dark-color !important;
			}
			&+.collapse,&+.collapsing{
				background: $white;
				z-index: 150;
				@include PC-md{
					position: absolute;
					left: 0;
					top: 32px;
				}
			}
		}
		.nav-link{
			background: none;
			padding: 0;
			width: 31px;
			height: 31px;
			display: flex;
			justify-content: center;
			align-items: center;
			img{
				width: 24px;
				filter: invert(51%) sepia(99%) saturate(886%) hue-rotate(164deg) brightness(98%) contrast(80%);
			}
			&.active{
				background: none;
				img{
					filter: invert(64%) sepia(35%) saturate(814%) hue-rotate(44deg) brightness(108%) contrast(76%);
				}
			}
		}
	}
	&-mode-toggler{
		&:before{
			content: "";
			width: 20px;
			height: 20px;
			display: inline-block;
			background: url("../images/ic-map.svg") no-repeat;
			background-size: 100%;
			display: inline-block;
			margin-right: 4px;
		}
		&:after{
			content: attr(data-con);
		}
		&.active{
			&:before{
				content: "";
				width: 20px;
				height: 20px;
				display: inline-block;
				background: url("../images/ic-list.svg") no-repeat;
				background-size: 100%;
				display: inline-block;
				margin-right: 4px;
			}
			&:after{
				content: "資料列表模式";
			}
		}
	}
	&.open {
		display: block;
	}
	&.hide {
		pointer-events: none;
	}
	&.smaller{
		height: 36px !important;
	}
	&.full{
		height: 100%;
	}
	.input-group {
		max-width: 350px;
	}
	iframe{
		height: 100%;
	}
	
}
.side-info-content{
	label{
		flex-shrink: 0;
		font-size: $font-sm;
		color: $secondary-xdark-color;
		text-align: right;
		@include mobile-lg{
			width: 100% !important;
			text-align: left;
			margin-bottom: 8px;
		}
	}
	.form-group{
		display: flex;
		align-items: center;
		@include mobile-lg{
			flex-wrap: wrap;
		}
	}
}
.side-info-subtitle{
	display: flex;
	align-items: end;
	border-bottom: 1px solid $tertiary-light-color;
	color: $quaternary-light-color;
	font-size: $font-smd;
	padding-bottom: 12px;
	margin-bottom: 12px;
	.btn{
		font-size: $font-xsm;
	}
}
.side-info-header {
	padding: 7px 24px;
	display: flex;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 100;
	@include mobile-md {
		padding: 7px 10px;
	}
	img {
		filter: brightness(150);
	}
}
.side-info-button {
	button {
		position: relative;
		z-index: 10;
	}
}
.status {
	border-radius: 4px 30px 30px 4px;
	display: flex;
	align-items: center;
	img {
		width: 18px;
	}
	&-txt {
		font-size: $font-md;
		@include PC {
			font-size: $font-sm;
		}
	}
}
.btn-sm {
	img {
		width: 20px;
	}
}
hr {
	margin: 25px 0;
}
/*------------------------表格開始----------------------------*/
.footable {
	background: $white;
	&>thead{
		position: sticky;
		top: 43px;
		z-index: 100;
		&:before{
			content: "";
			display: block;
			position: absolute;
			width: 100%;
			height: 8px;
			background: $white;
			left: 0;
			top: -8px;
		}
	}
	td{
		border-width: 0;
	}
	&[data-header="no-sticky"]{
		&>thead{
			position:initial;
			top: auto;
			&:before{
				display: none;
			}
		}
	}
	.footable-detail-row {
		background: $gray100;
	}
	.footable-details {
		background: $tertiary-xlight-color;
		tbody {
			tr {
				border-bottom: 1px solid $gray300;
			}
			th {
				background: $secondary-color;
				color: $white;
				text-align: right;
				vertical-align: middle;
			}
			td {
				text-align: left;
				height: 40px;
			}
		}
	}
	.footable-filtering {
		position: relative;
		z-index: 100;
		th {
			background: $pale-color;
		}
		&-search {
			display: flex;
			justify-content: flex-end;
			.input-group {
				max-width: 250px;
				input {
					border-radius: 50rem;
					padding-left: 20px;
					border-right: none;
				}
				.input-group-btn {
					button:first-child {
						border-radius: 0 50rem 50rem 0;
						background: $white;
						border-left: 0;
						border: 1px solid $primary-light-color;
						border-left: none;
						background: url("../images/ic-search-blue.svg") no-repeat center $white;
						background-size: 20px;
						width: 37px;
						height: 37px;
						margin-right: 8px;
						span {
							display: none;
						}
					}
					button:nth-child(2) {
						width: 37px;
						height: 37px;
						border-radius: 50%;
						background: url("../images/ic-filter.svg") no-repeat center $primary-color;
						&::after {
							display: none;
						}
					}
				}
			}
		}
	}
}
.table-modal{
		th{
			color: $quaternary-color !important;
			background: $quaternary-xlight-color !important;
		}
		&> tbody > tr:nth-child(even){
			background: $white !important;
		}
		td{
			border-color: $tertiary-light-color;
		}
		tr:last-child{
			td{
				border: 0;
			}
		}
	}
.input-group-btn.open {
	position: relative;
	z-index: 1000;
	button:nth-child(2) {
		background: url("../images/ic-filter.svg") no-repeat center $secondary-color !important;
	}
	.dropdown-menu {
		display: block;
		position: absolute;
		right: 0;
		top: 40px;
		box-shadow: 0 0 5px $gray300;
		padding: 16px;
		display: flex;
		flex-wrap: wrap;
		width: 350px;
		border-radius: 8px;
		@include mobile-lg {
			width: 300px;
		}
		@include mobile-md {
			width: 280px;
		}
		li {
			width: 50%;
			&:last-child {
				display: none;
			}
		}
		label {
			color: $gray700;
		}
		input[type="checkbox"] {
			width: 14px;
			height: 14px;
			border: 1px solid $white !important;
			margin-top: 4px;
			position: relative;
			&:before {
				content: "";
				width: 16px;
				height: 16px;
				border: 1px solid $gray500 !important;
				background: $white;
				border-radius: 3px;
				overflow: hidden;
				margin-right: 16px;
				overflow: hidden;
				position: absolute;
				left: -1px;
				top: 0;
			}
		}
		input[type="checkbox"]:checked {
			border-radius: 3px;
			background: none;
			border: none;
			&:after {
				content: "";
				width: 16px;
				height: 16px;
				background: url("../images/ic-check-light.svg") no-repeat center $secondary-color;
				background-size: 10px;
				position: absolute;
				left: -1px;
				top: 0;
				border-radius: 3px;
			}
			color: $secondary-color !important;
			background: $secondary-color !important;
		}
	}
}

.info-table-wrapper {
	height: calc(100% - 50px);
	overflow: auto;
	thead {
		position: sticky;
		top: -2px;
		z-index: 1;
	}
}
.filter-btn {
	&.collapsed {
		background: $primary-color !important;
		border: 1px solid $primary-color !important;
	}
}

.filter-box {
	position: absolute;
	right: 16px;
	top: 60px;
	z-index: 3;
	width: 345px;
	box-shadow: 0 0 8px $gray500;
	border: 0;
	border-radius: 5px;
	background: $white;
	overflow: hidden;
	@include mobile-lg {
		width: 280px;
		top: 100px;
		right: 10px;
	}
	&:before {
		content: "";
		position: absolute;
		display: block;
		width: 22px;
		height: 13px;
		background: url("../images/ic-point.svg") no-repeat;
		right: 12px;
		top: -13px;
		@include mobile-lg {
			right: 5px;
		}
	}
}

.filter-box-item {
	width: 33.33%;
	@include mobile-lg {
		width: 50%;
	}
}
